.about {
   height: 128vh;
   padding-top: 70px;
   display: flex;
   justify-content: center;
   align-items: center;
   background-color: #181717;
}
.aboutImg {
   width: 50%;
   height: 100%;
   background-image: url('./img/banner_menber_01.jpg');
   background-size: cover;
}

.cardImg{
   width: 100%;
   padding: 0;
   display: flex;
   justify-content: center;
   align-items: center;
}
.cardImg img{
   width: 100%;
   max-width: 400px;
   
}
.inner-section {
   width: 50%;
   height: 100%;
   padding: 2em;
   display: flex;
   justify-content: center;
   flex-direction: column;
   background-color: #302e2e;
}
@media screen and (max-width: 600px) {
   .aboutImg {
      display: none;
   }
   .inner-section {
      width: 100%;
   }
}

.inner-section h1 {
   margin-bottom: 30px;
   font-size: 30px;
   font-weight: 900;
}
.how {
   font-size: 25px;
   color: #ffffff;
   line-height: 20px;
   margin-bottom: 55px;
}
.titulo {
   font-size: 45px;
   color: #ffffff;
   line-height: 20px;
   margin-bottom: 55px;
}
.img {
   border-radius: 15px;
   box-shadow: 0 0 5px #4b4b4b, 0 0 40px #4b4b4b, 0 0 80px #4b4b4b;
}
.text {
   padding: 1em;
   font-size: 18px;
   color: #ffffff;
   line-height: 30px;
}

.btn-purchase2,
.btn-purchase {
   font-size: 22px;
   text-align: center;
   letter-spacing: 1px;
   border: none;
   border-radius: 95px;
   padding: 8px;
   width: 200px;
   background-color: #191a1ac9;
   color: #e8c23a;
   cursor: pointer;
   transition: all 0.3s ease;
}
.btn-purchase2:hover {
   background: #caa838;
   color: #fff;
}
