/*Cards*/
a{
   text-decoration: none;
}
.container-card {
   padding: 2em;
   width: 100%;
   display: flex;
   max-width: 1100px;
   margin: auto;
}
.title-cards {
   width: 100%;
   max-width: 1080px;
   margin: auto;
   padding: 20px;
   margin-top: 20px;
   text-align: center;
   color: #ffffff;
}
.card {
   width: 100%;
   border-radius: 1px;
   overflow: hidden;
   background: rgba(255, 255, 255, 0);
   box-shadow: 0px 1px 10px rgba(0, 0, 0, 0.2);
   transition: all 400ms ease-out;
   cursor: default;
   border: 1px solid #ffffff86;
}
.card:hover {
   box-shadow: 5px 5px 20px rgba(0, 0, 0, 0.4);
   transform: translateY(-3%);
}
.card img {
   width: 100%;
   height: 210px;
}
.card .contenido-card {
   padding: 15px;
   text-align: center;
}
.card .contenido-card h3 {
   margin-bottom: 15px;
   color: #caa838;
}
.card .contenido-card p {
   line-height: 1.8;
   color: #ffffff;
   font-size: 14px;
   margin-bottom: 5px;
}
.card .contenido-card button {
   display: inline-block;
   padding: 10px;
   margin-top: 10px;
   text-decoration: none;
   color: #caa838;
   border: 1px solid #caa838;
   border-radius: 4px;
   transition: all 400ms ease;
   margin-bottom: 5px;
}
.card .contenido-card button:hover {
   background: #caa838;
   color: #fff;
}
@media only screen and (min-width: 320px) and (max-width: 768px) {
   .container-card {
      flex-wrap: wrap;
   }
   .card {
      margin: 15px;
   }
}
